  /* Github css */

  @font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 100;
    src: local('Roboto Thin'), local('Roboto-Thin'),
      url(http://themes.googleusercontent.com/static/fonts/roboto/v10/vzIUHo9z-oJ4WgkpPOtg1_esZW2xOQ-xsNqO47m55DA.woff)
        format('woff');
  }

  p {
    margin: 1em 0;
  }

  a {
    color: #4183c4;
    text-decoration: none;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
    padding: 0 0 10px 0;
    font-weight: bold;
    -webkit-font-smoothing: subpixel-antialiased;
    cursor: text;
    --h1-font-size: 30px;
    --h2-font-size: 24px;
    --h3-font-size: 18px;
    --h4-font-size: 16px;
    --h5-font-size: 14px;
    --h6-font-size: 14px;
  }
  h1 {
    font-size: var(--h1-font-size);
    line-height: var(--h1-font-size);
    color: #1e3a56;
    text-align: center;
  }

  h2 {
    font-size: var(--h2-font-size);
    line-height: var(--h2-font-size);
    padding-bottom: 0;
    margin-bottom: 10px;
    border-bottom: 1px solid #ccc;
    color: #476d96;
  }

  h3 {
    font-size: var(--h3-font-size);
    line-height: var(--h3-font-size);
    color: #498265;
    -webkit-margin-before: 0.01em;
    -webkit-margin-after: 0.01em;
  }

  h4 {
    font-size: var(--h4-font-size);
    line-height: var(--h4-font-size);
    color: #333;
    text-align: center;
    -webkit-margin-before: 0.02em;
    -webkit-margin-after: 0.02em;
  }

  h5 {
    font-size: var(--h5-font-size);
    line-height: var(--h5-font-size);
    color: #1e3a56;
    text-align: center;
  }

  h6 {
    font-size: var(--h6-font-size);
    line-height: var(--h6-font-size);
    color: #4b5b6b;
    text-align: center;
  }

  p,
  blockquote,
  table,
  pre {
    margin: 0 0 10px 0;
  }

  ul {
    padding-left: 30px;
    margin: 0 0 10px 0;
  }

  ol {
    padding-left: 30px;
  }

  ol li ul:first-of-type {
    margin-top: 0;
  }

  hr {
    background: transparent
      url(data:image/png;base64,)
      repeat-x 0 0;
    border: 0 none;
    color: #ccc;
    height: 4px;
    padding: 0;
  }

  .resume > h2:first-child {
    margin-top: 0;
    padding-top: 0;
  }

  .resume > h1:first-child {
    margin-top: 0;
    padding-top: 0;
  }

  .resume > h1:first-child + h2 {
    margin-top: 0;
    padding-top: 0;
  }

  .resume > h3:first-child,
  .resume > h4:first-child,
  .resume > h5:first-child,
  .resume > h6:first-child {
    margin-top: 0;
    padding-top: 0;
  }

  a:first-child h1,
  a:first-child h2,
  a:first-child h3,
  a:first-child h4,
  a:first-child h5,
  a:first-child h6 {
    margin-top: 0;
    padding-top: 0;
  }

  h1 + p,
  h2 + p,
  h3 + p,
  h4 + p,
  h5 + p,
  h6 + p,
  ul li > :first-child,
  ol li > :first-child {
    margin-top: 0;
  }

  dl {
    padding: 0;
  }

  dl dt {
    font-size: 14px;
    font-weight: bold;
    font-style: italic;
    padding: 0;
    margin: 15px 0 5px;
  }

  dl dt:first-child {
    padding: 0;
  }

  dl dt > :first-child {
    margin-top: 0;
  }

  dl dt > :last-child {
    margin-bottom: 0;
  }

  dl dd {
    margin: 0 0 15px;
    padding: 0 15px;
  }

  dl dd > :first-child {
    margin-top: 0;
  }

  dl dd > :last-child {
    margin-bottom: 0;
  }

  blockquote {
    border-left: 4px solid #ddd;
    padding: 0 15px;
    color: #777;
  }

  blockquote > :first-child {
    margin-top: 0;
  }

  blockquote > :last-child {
    margin-bottom: 0;
  }

  $table-padding: 0px 13px 6px 0px;

  table {
    border-collapse: collapse;
    border-spacing: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: top;
    $table-padding: 0px 13px 6px 0px;

    colgroup {
      &:first-child {
        width: 8%;
      }
      &:nth-child(2) {
        width: 91%;
      }
    }

    th {
      font-weight: bold;
      border: none;
      vertical-align: top;
      padding: $table-padding;
    }

    td {
      border: none;
      vertical-align: top;
      padding: $table-padding;
    }

    td .td-center {
      text-align: center;
    }

    tr {
      border: none;
      vertical-align: top;
    }
  }

  img {
    max-width: 100%;
  }

  code,
  tt {
    margin: 0 2px;
    padding: 0 5px;
    white-space: nowrap;
    border: 1px solid #eaeaea;
    background-color: #f8f8f8;
    border-radius: 3px;
    font-family: Consolas, 'Liberation Mono', Courier, monospace;
    font-size: 12px;
    color: #333;
  }

  pre > code {
    margin: 0;
    padding: 0;
    white-space: pre;
    border: 0;
    background: transparent;
  }

  .highlight pre {
    background-color: #f8f8f8;
    border: 1px solid #ccc;
    font-size: 13px;
    line-height: 19px;
    overflow: auto;
    padding: 6px 10px;
    border-radius: 3px;
  }

  pre {
    background-color: #f8f8f8;
    border: 1px solid #ccc;
    font-size: 13px;
    line-height: 19px;
    overflow: auto;
    padding: 6px 10px;
    border-radius: 3px;
  }

  pre code,
  pre tt {
    background-color: transparent;
    border: 0;
  }

  .poetry pre {
    font-family: Georgia, Garamond, serif !important;
    font-style: italic;
    font-size: 110% !important;
    line-height: 1.6em;
    display: block;
    margin-left: 1em;
  }

  .poetry pre code {
    font-family: Georgia, Garamond, serif !important;
    word-break: break-all;
    word-break: break-word;
    -webkit-hyphens: auto;
    -moz-hyphens: auto;
    hyphens: auto;
    white-space: pre-wrap;
  }

  sup,
  sub,
  a.footnote {
    font-size: 1.4ex;
    height: 0;
    line-height: 1;
    vertical-align: super;
    position: relative;
  }

  sub {
    vertical-align: sub;
    top: -1px;
  }

  @media print {
    .resume {
      background: #fff;
    }

    img,
    pre,
    blockquote,
    table,
    figure {
      page-break-inside: avoid;
    }

    .resume {
      background: #fff;
      border: 0;
    }

    code {
      background-color: #fff;
      color: #333 !important;
      padding: 0 0.2em;
      border: 1px solid #dedede;
    }

    pre {
      background: #fff;
    }

    pre code {
      background-color: white !important;
      overflow: visible;
    }
  }

  @media screen {
    .resume.inverted {
      color: #eee !important;
      border-color: #555;
      box-shadow: none;
    }

    .inverted .resume,
    .inverted hr .inverted p,
    .inverted td,
    .inverted li,
    .inverted h1,
    .inverted h2,
    .inverted h3,
    .inverted h4,
    .inverted h5,
    .inverted h6,
    .inverted th,
    .inverted .math,
    .inverted caption,
    .inverted dd,
    .inverted dt,
    .inverted blockquote {
      color: #eee !important;
      border-color: #555;
      box-shadow: none;
    }

    .inverted td,
    .inverted th {
      background: #333;
    }

    .inverted h2 {
      border-color: #555;
    }

    .inverted hr {
      border-color: #777;
      border-width: 1px !important;
    }

    ::selection {
      background: rgba(157, 193, 200, 0.5);
    }

    h1::selection {
      background-color: rgba(45, 156, 208, 0.3);
    }

    h2::selection {
      background-color: rgba(90, 182, 224, 0.3);
    }

    h3::selection,
    h4::selection,
    h5::selection,
    h6::selection,
    li::selection,
    ol::selection {
      background-color: rgba(133, 201, 232, 0.3);
    }

    code::selection {
      background-color: rgba(0, 0, 0, 0.7);
      color: #eee;
    }

    code span::selection {
      background-color: rgba(0, 0, 0, 0.7) !important;
      color: #eee !important;
    }

    a::selection {
      background-color: rgba(255, 230, 102, 0.2);
    }

    .inverted a::selection {
      background-color: rgba(255, 230, 102, 0.6);
    }

    td::selection,
    th::selection,
    caption::selection {
      background-color: rgba(180, 237, 95, 0.5);
    }

    .inverted {
      background: #0b2531;
      background: #252a2a;
    }

    .inverted .resume {
      background: #252a2a;
    }

    .inverted a {
      color: #acd1d5;
    }
  }

